.site-footer {
  background: url(#{$path}/images/footer--mobile@2x.jpg) no-repeat 50% 100%;
  background-size: auto 100%;
  height: 62rem;
  margin: 0 auto;
  position: relative;

  @media (min-width: map-get($breakpoints, 'small')) {
    background-image: url(#{$path}/images/footer--small.jpg);
  }

  @media (min-resolution: 120dpi) and (min-width: map-get($breakpoints, 'small')) {
    background-image: url(#{$path}/images/footer--small@2x.jpg);
  }

  @media (min-width: map-get($breakpoints, 'medium')) {
    background-image: url(#{$path}/images/footer--medium.jpg);
  }

  @media (min-resolution: 120dpi) and (min-width: map-get($breakpoints, 'medium')) {
    background-image: url(#{$path}/images/footer--medium@2x.jpg);
  }

  @include respond-to('medium-large') {
    background-image: url(#{$path}/images/footer--large.jpg);
    background-repeat: repeat-x;
    height: 41rem;
  }

  @media (min-resolution: 120dpi) and (min-width: map-get($breakpoints, 'medium-large')) {
    background-image: url(#{$path}/images/footer--large@2x.jpg);
  }

  &::before {
    @include pseudo;
    background: linear-gradient(
      to bottom,
      rgba($color--white, 1) 20%,
      rgba($color--white, 0) 100%
    );
    height: 100%;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 0;
  }
}

.site-footer__inner {
  padding-top: unit(2);
  position: relative;
  z-index: 1;

  @include respond-to('medium-large') {
    display: flex;
    margin: 0 auto;
    max-width: 83.33333%;
    padding-top: unit(3);
  }
}

.site-footer__col-1 {
  @include null-margins;
  font-size: 1.3rem;
  line-height: 1.4;
  margin: 0 auto;
  max-width: 280px;
  text-align: center;

  @include respond-to('small') {
    max-width: 300px;
  }

  @include respond-to('medium-large') {
    max-width: none;
    padding-right: unit(2);
    text-align: left;
  }

  @include respond-to('large') {
    padding-left: 10rem;
  }
}

.site-footer__col-2 {
  @include clearfix;
  @include null-margins;
  margin: 0 auto;
  max-width: 300px;
  padding-top: unit(0.5);

  @include respond-to('medium-large') {
    padding-top: 0;
    width: 40%;
  }
}

.site-footer__logo {
  display: inline-block;
  line-height: 0;
  margin: 0 auto;

  @include respond-to('medium-large') {
    margin-left: 0;
  }

  @include respond-to('large') {
    margin-left: -10rem;
  }

  img {
    height: auto;
    max-width: 280px;

    @include respond-to('small') {
      max-width: 300px;
    }

    @include respond-to('medium-large') {
      max-width: 430px;
    }

    @include respond-to('large') {
      max-width: 530px;
    }
  }
}

.site-footer__compulsory,
.site-footer__menu,
.site-footer__external,
.site-footer__social {
  @include reset_list;

  li {
    line-height: 1.1;
    margin: 0;

    + li {
      margin-top: unit(0.4);
    }
  }
}

.site-footer__compulsory {
  font-size: 1.2rem;
  font-weight: $font-weight--bold;
  width: 100%;

  &.-small {
    clear: both;
    margin: 0;
    padding-top: unit(1.5);
  }

  &.-large {
    display: none;
  }

  @include respond-to('medium-large') {
    font-size: 1.3rem;

    &.-small {
      display: none;
    }

    &.-large {
      display: block;
    }
  }

  a {
    @include hover-underline;
    color: $color--fuscous-gray;
  }

  li {
    display: inline;
    padding-right: unit(0.4);

    @include respond-to('medium-large') {
      padding-right: unit(0.5);
    }
  }
}

.site-footer__menu,
.site-footer__social,
.site-footer__external {
  a {
    @include hover-underline;
    font-size: 1.2rem;
    font-weight: $font-weight--bold;
  }
}

.site-footer__menu {
  float: left;
  margin: 0;
  width: calc(45% - #{unit(0.5)});

  a {
    color: $color--fuscous-gray;
    text-transform: uppercase;
  }
}

.site-footer__social {
  clear: right;
  float: right;
  margin: 0;
  width: calc(55% - #{unit(0.5)});

  a {
    align-items: center;
    color: $color--venice-blue;
    display: flex;
  }

  img {
    height: 18px;
    margin-right: unit(0.6);
    width: 18px;
  }

  li + li {
    margin-top: unit(1);
  }
}

.site-footer__external {
  clear: left;
  float: left;
  margin: unit(1) 0 0;
  width: calc(40% - #{unit(0.5)});

  a {
    color: $color--venice-blue;
    text-transform: uppercase;
  }
}
